.card {
  display: flex;
  width: 300px;
  margin: 10px 70px;
  border: 1.5px solid #000;
  border-radius: 12px;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.5);
}
img {
  width: 6rem;
  padding: 5px;
  border-radius: 12px;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.5);
}
h3 {
  flex-grow: 1;
  text-align: center;
  align-self: center;
}
